.CardCertificacion {
    margin: 0 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    border-radius: 10px 5%;
    padding: 25px 20px;
    height: 100%;

}

.imgcarCertication {
    text-align: center !important;
    padding-bottom: 10px;
    height: 45%;
}

.TittleCardCertification {
    text-align: center !important;
    font-size: 18px !important;
    font-weight: bolder !important;
    padding-bottom: 5px;
}

.TextCardCertification {
    text-align: center !important;
    font-size: 14px !important;
}

.contenedorCertificadosCard {}

.cardUnidadCertificado {
    max-width: 20%;
}

.textCertification {
    font-weight: bolder !important;
    font-size: 20px !important;
}

.TextCardCetification {
    text-align: center;
    
    @media (max-width: 1560px) {
        margin: 8px;
        height: 40%;
    }
    @media (max-width: 1250px) {
        margin: 10px;
        height: 40%;
    }

    @media (max-width: 900px) {
        margin-top: 20px;
    }

    @media (max-width: 616px) {
        margin-top: 5px;
    }

    @media (max-width: 455px) {
        margin-top: 5px;
    }
    @media (max-width: 393px) {
        margin-top: -14px;
    }
    @media (max-width: 343px) {
        margin-top: -40px;
    }
}

.ButtonsCardCertification {
    text-Align: center;
    height: 15%;

    @media (max-width: 1050px) {
        margin-top: 8px;
    }

    @media (max-width: 900px) {
        margin-top: -20px;
    }

    @media (max-width: 616px) {
        margin-top: -5px;
    }

    @media (max-width: 455px) {
        margin-top: 5px;
    }

    @media (max-width: 393px) {
        margin-top: 28px;
    }
    @media (max-width: 343px) {
        margin-top: 45px;
    }

}